.LoginSignupContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0;
    left: 0;

}

.LoginSignupBox {
    background-color: #fff;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;

}

.login_signup_toggle {
    display: flex;
    height: 3vmax;
}

.login_signup_toggle>p {
    color: rgba(0, 0, 0, 0.687);
    font: 300 1vmax "Roboto";
    transition: all 0.5s;
    cursor: pointer;
    display: grid;
    place-items: center;
    width: 100%;

}

.login_signup_toggle>p:hover {
    color: tomato;
}

.LoginSignupBox>div>button {
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 0.5s;

}

.loginForm,
.signupForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    justify-content: space-evenly;
    transition: all 0.5s;

}
 .signupForm{
    transform: translateY(-100%) translateX(-100%);
 }

.loginForm>div,
.signupForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.loginForm>div>input,
.signupForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;

}


.loginForm>div>svg,
.signupForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.687);
}

.loginForm>a {
    color: rgba(0, 0, 0, 0.687);
    text-decoration: none;
    align-self: flex-end;
    transition: all 0.5s;
    font: 500 1vmax "Gill Sans";
}

.loginForm>a:hover {
    color: #000;
}

#registerImage > img{
width: 3vmax;
border-radius:100% ;
padding: 0.5vmax;
background-color: rgba(204, 204, 204, 0.532);
margin-right: 0.5vmax;
}

#registerImage > input{
    display: flex;
    padding: 0;

}

#registerImage > input::file-selector-button{
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.623);
    background-color: #fff;

}

#registerImage > input::file-selector-button:hover{
    background-color: rgb(230, 225, 225);
}
.loginBtn,
.signupBtn {
    border: none;
    background-color: tomato;
    color: #fff;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}


.loginBtn:hover,
.signupBtn:hover {
    background-color: rgb(179, 66, 46);

}

.shiftToLeft{
    transform: translateX(-100%);
}

.shiftToNeutral{
    transform: translateX(0%);

}

.shiftToNeutralForm{
    transform: translateX(0%) translateY(-100%); 
}

.shiftToRight{
    transform: translateX(100%);
}

@media screen and (max-width:600px) {
 .LoginSignupContainer{
    background-color: #fff;
 }   

 .LoginSignupBox{
    width: 100vw;
    height: 95vh;

 }

 .login_signup_toggle{
    height: 5vmax;
 }
 .login_signup_toggle > p{
    font: 300 2vmax "Roboto";
 }
 .loginForm,
 .signupForm{
    padding: 5vmax;
 }

 .loginForm>div>input,
 .signupForm>div>input{
    padding: 2.5vmax 7vmax;
    font:300 2vmax cursive;
 }
 .loginForm>div>svg,
 .signupForm>div>svg{
    font-size:2.8vmax;
    padding: 0 1.5vmax;
 }
 .loginForm>a{
    font: 500 2vmax "Gill Sans";
 }
 #registerImage > img{
    width: 9vmax;
    border-radius: 100%;
    margin-right:1vmax ;
 }

 #registerImage > input::file-selector-button{
    height: 7vh;
    font: 400 1.8vmax cursive;
 }
 .loginBtn,
 .signupBtn{
    font: 300 2vmax "Roboto";
    padding: 2vmax;
 }
}
